.navbar-bg {
   animation-name: fade;
    animation-duration: 3s;
    display: flex;
    justify-content: center;
    max-width: 100%;
    position: sticky;
    top: 0;
    background-color: #000000bb;
    z-index: 100;
}

.navbar {
    animation-name: fade;
    animation-duration: 3s;
    display: flex;
    justify-content: space-between;
    max-width: 1600px;
    height: 6vh;
    padding: 0.5rem 1rem;
}

.navbar {
    width: 1600px;
}

.navbar__wrapper {
    align-items: center;
    display: flex;
    gap: 0.5rem;
}

.navbar__logo {
    width: 2rem;
    cursor: pointer;
}

.navbar__link {
    font-size: 1.2rem;
    color: var(--light-bg);
    text-decoration: none;
}

.navbar__link:hover {
    color: var(--primary-style-color);
    transition: color .5s;
}

.navbar__select-toggle {
    align-items: center;
    display: flex;
    gap: 2rem;

    .css-qc6sy-singleValue {
        color: var(--light-bg);
    }

    .css-1pahdxg-control,
    .css-1s2u09g-control {
        background-color: transparent;
        border: none;
        box-shadow: none;
    }

    .css-319lph-ValueContainer,
    .css-tlfecz-indicatorContainer,
    .css-1gtu0rj-indicatorContainer {
        padding: 0;
    }

    .css-tlfecz-indicatorContainer {
        color: var(--primary-style-color)
    }

    .css-1gtu0rj-indicatorContainer {
        color: #420A1A;
    }

    .css-2613qy-menu {
        border: none;
    }

    .css-1okebmr-indicatorSeparator {
        display: none;
    }
    
}

.navbar__select {
    background-color: transparent;
    border: none;
    color: var(--light-bg);
    font-size: 1.2rem;
    font-weight: 600;
}

.navbar__select option {
    color: var(--full-dark);
}

.navbar__toggle {
    align-items: center;
    border: 2px solid var(--primary-style-color);
    background-color: #420A1A;
    border-radius: 3rem;
    color: var(--primary-style-color);
    cursor: pointer;
    display: flex;
}

.navbar__toggle--text {
    font-size: 1.3rem;
    margin-right: 0.5rem;
    margin-top: 3px;
    margin-left: 0.5rem;
}

.navbar__toggle--dark {
    border-radius: 50%;
    outline: 2px solid var(--primary-style-color);
    padding: 6px;
}

@keyframes fade {
    from {
        opacity: 0;;
    }

    to {
        opacity: 1;
    }
}

/*============== Media Querys =============*/

@media only screen and (max-width: 768px) {
    .navbar {
        display: none;
    }
}